<template>
  <div id="smallClass">
    <head-top title="微课"></head-top>

    <div v-if="dataList && dataList.length > 0">
      <div v-for="(item, index) in dataList" :key="index" class="list_item">
        <t-video
          :ref="'video'+index"
          :src="item.src"
          :poster="item.poster"
          @selectChange="change($event, index)"
        ></t-video>
        <div class="line_mark">
          <span>{{item.typeName}}</span>
          <span v-if="item.title">{{item.title}}</span>
          <span v-else>{{item.time | formatDate(item.time)}}</span>
        </div>
      </div>
    </div>
    <r-lack-data v-else></r-lack-data>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import tVideo from "@/components/t-video";
import defaultBg from "@/assets/video/video.png";
import { microLessonTypeName } from "@/utils/enum";

export default {
  name: "",
  components: { headTop, tVideo },
  props: {},
  data() {
    return {
      selectIndex: -1,
      dataList: []
    };
  },
  mounted() {},
  created() {
    this.getList();
  },
  methods: {
    getList() {
      if (this.$config.isTest) {
        this.dataList = [
          {
            title: "英语练习",
            time: "2019-09-01 10:35:24",
            typeName: "课件",
            src: "http://vjs.zencdn.net/v/oceans.mp4",
            poster: defaultBg //"https://photo.mac69.com/180205/18020526/a9yPQozt0g.jpg"
          },
          {
            title: "英语练习",
            time: "2019-09-01 10:35:24",
            typeName: "课件",
            src: "http://vjs.zencdn.net/v/oceans.mp4",
            poster: defaultBg //"https://photo.mac69.com/180205/18020526/a9yPQozt0g.jpg"
          }
        ];
      } else {
        this.dataList = [];
        this.$mainHttp.miniCourse
          .getList({ courseID: this.$store.getters.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.dataList.push({
                  title: item["MLTitle"],
                  time: item["StartTime"],
                  typeName: microLessonTypeName(item["MLType"]),
                  src: this.$gatewayHttp.resourse.video(
                    item["MLID"],
                    item["FileFormat"]
                  ),
                  poster: defaultBg
                });
              });
            }
            this.isLoading = false;
          })
          .catch(err => {
            this.isLoading = false;
          });
        this.$store.commit("SET_NOTEREFRESH", false);
      }
    },
    change(e, index) {
      if (this.selectIndex !== -1) {
        if (this.selectIndex !== index) {
          this.$refs["video" + this.selectIndex][0].stopPlay();
          this.selectIndex = index;
        }
      } else {
        this.selectIndex = index;
      }
    }
  }
};
</script>

<style lang='less'>
#smallClass {
  width: 100%;
  height: 100%;
  background-color: #fff;

  .list_item {
    margin-top: 2vh;
  }

  .video-player {
    width: 94vw;
    min-height: 33vh;
    // padding-top: 2vh;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    border-radius: 1rem;

    video {
      width: 100%;
      margin: 0 auto;
    }
  }
  .line_mark {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin: 0 3vw;
    font-size: 1.25rem;
    line-height: 3;
    color: #777;
    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 1px;
      border-top: 1px solid #999;
      transform: scaleY(0.6);
    }
  }
}
</style>


